os. El carácter único de estos derechos se reconoce debido a la importancia que tiene la infancia y la adolescencia en el desarrollo futuro de la sociedad y el mundo.")])])]),_c('div',{staticClass:"hr--blank"}),_c('div',{staticClass:"titulo-segundo mb-5"},[_c('div',{staticClass:"h4",attrs:{"id":"t_1_1"}},[_vm._v("1.1 Las niñas, niños y adolescentes como sujetos titulares de derechos")])]),_c('div',{staticClass:"c-tarjeta c-tarjeta--celeste mb-5"},[_c('div',{staticClass:"row justify-content-center align-items-center"},[_c('div',{staticClass:"col-auto mb-4 mb-lg-0"},[_c('figure',[_c('img',{staticStyle:{"max-width":"350px","display":"block","margin":"0 auto"},attrs:{"src":require("@/assets/tema_1/img_1_1_01.svg"),"alt":"Las niñas, niños y adolescentes como sujetos titulares de derechos"}})])]),_c('div',{staticClass:"col col-lg"},[_c('p',[_vm._v("Como se mencionó anteriormente, las niñas, niños y adolescentes poseen un conjunto de derechos específicos en titularidad. Esto significa que el Estado reconoce que los niños, niñas y adolescentes tienen la capacidad de decidir, actuar y opinar sobre todos los aspectos de su vida en forma autónoma, informada y libre, sin la necesidad de que un tercero lo haga en su lugar a pesar de la protección que deben bridarle (Amnistía Internacional, s. f.). ")])])])]),_c('p',[_vm._v("En épocas anteriores esta posibilidad era negada al considerarse que la infancia y la adolescencia no podían tener una representación jurídica plena por estar bajo el cuidado de padres, madres o tutores y, en consecuencia, no poseían el derecho a elegir y participar libremente. Con esta visión, los niños, niñas y adolescentes eran considerados un objeto de cuidado que solo al convertirse en adultos podrían ejercer plenamente su rol como ciudadanos (Gómez de la Torre, 2018). A este enfoque se le conoce como “paradigma de la protección tutelar” (De la Iglesia et ál., 2008) y actualmente no hace parte de los marcos normativos del Estado colombiano. "),_c('br'),_